import Vue from 'vue'
import App from './App'
import store from './store/index'
import router from './router'
import axios from 'axios'
import $ from 'jquery'

Vue.config.productionTip = false;
axios.defaults.withCredentials = true ;//允许跨域
global.axios = axios ;//设置为全局引用
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
});




