import  Vue from 'vue'
import  Vuex from 'vuex'
import actions from './actions'
import getter from './getter'
import mutations from './mutations'
Vue.use(Vuex);
const state = {
  navigation:false, //改变导航栏状态
  about_nav:2, //关于导航状态
  products_page:'',//产品类型
  solution_nav:'', //解决导航状态
  products_nav:41, //产品导航状态
  second_nav:0, //二级导航状态
  indexText:[], //页面导航栏数据
};
export default new Vuex.Store({
   state,
   mutations,
   actions,
   getter
});



