<template>
    <div class="headerCenter">
      <div class="heads" :class="[addClassToBg ? activeClass : '']">
        <div class="headContent" >
          <div class="headLeft" @click="skipPage(1)">
            <img src="../../../static/icon/logo.png" alt="">
          </div>
          <div class="headRight">
            <ul>
              <li class="nav-item" @click="skipPage(1)">首页</li>
              <li  class="nav-item " @mouseenter="navShow(1)" @mouseleave="navLeave(1)" >
                <span @click="skipPage(2)">关于我们</span>
                <div class="select" v-show="about">
                  <ul>
                    <li :class="[activeNum===2 ? addBg :'']" @click="skipPage(2)">企业简介</li>
                    <li :class="[activeNum===21 ? addBg :'']"  @click="skipPage(21)">愿景与使命</li>
                  </ul>
                </div>
              </li>
              <li  class="nav-item " @mouseenter="navShow(2)" @mouseleave="navLeave(2)">
                <span @click="skipPage(31)">精选系列</span>
                <div class="select" v-show="plan">
                  <ul>
                    <li :class="[activeNum===31 ? addBg :'']" @click="skipPage(31)">精选龙头</li>
                    <li :class="[activeNum===32 ? addBg :'']" @click="skipPage(32)">精选五金</li>
                    <li :class="[activeNum===33 ? addBg :'']" @click="skipPage(33)">精选智能</li>
                    <li :class="[activeNum===34 ? addBg :'']" @click="skipPage(34)">精选定制</li>
                  </ul>
                </div>
              </li>
              <li class="nav-item " @mouseenter="navShow(3)" @mouseleave="navLeave(3)">
                <span  @click="skipPage(41)">产品中心</span>
                <div class="select" v-show="product">
                  <ul>
                    <li :class="[activeNum===41 ? addBg :'']"  @click="skipPage(41)">浴室系列</li>
                    <li :class="[activeNum===43 ? addBg :'']"  @click="skipPage(43)">五金系列</li>
                    <li :class="[activeNum===44 ? addBg :'']"  @click="skipPage(44)">定制系列</li>
                    <li :class="[activeNum===45 ? addBg :'']"  @click="skipPage(45)">智能系列</li>
                    <li :class="[activeNum===46 ? addBg :'']"  @click="skipPage(46)">其他</li>
                  </ul>
                </div>
              </li>
              <li class="nav-item" ><span @click="skipPage(24)">联系我们</span></li>
            </ul>
          </div>
        </div>
      </div>
      <div class="mobileHeads">
        <div class="m-logo"  @click="skipPage(1)"><img src="../../../static/icon/logo.png" alt=""></div>
        <div class="m-bottom">
          <div class="m-spanBottom" @click="clickMuneShow()"><img src="../../../static/icon/buttom.png" alt=""></div>
          <div class="m-headRight" v-if="headerShow">
            <ul>
              <li class="m-nav-item" @click="skipPage(1)">首页</li>
              <li  class="m-nav-item"><span @click="navShow(1)">关于我们</span></li>
              <li  class="m-nav-item"><span @click="navShow(2)">精选系列</span></li>
              <li class="m-nav-item"><span  @click="navShow(3)">产品中心</span></li>
              <li class="m-nav-item" ><span @click="navShow(4)">联系我们</span></li>
            </ul>
          </div>
          <div class="m-headRight-two">
            <ul>
              <li  class="m-nav-item-two">
                <div class="m-select-two" v-show="about">
                  <ul>
                    <li @click="backIndex()">上一级</li>
                    <li @click="skipPage(2)">企业简介</li>
                    <li @click="skipPage(21)">愿景与使命</li>
                  </ul>
                </div>
              </li>
              <li  class="m-nav-item-two">
                <div class="m-select-two" v-show="plan">
                  <ul>
                    <li @click="backIndex()">上一级</li>
                    <li @click="skipPage(31)">精选龙头</li>
                    <li @click="skipPage(32)">精选五金</li>
                    <li @click="skipPage(33)">精选智能</li>
                    <li @click="skipPage(34)">精选定制</li>
                  </ul>
                </div>
              </li>
              <li  class="m-nav-item-two">
                <div class="m-select-two" v-show="product">
                  <ul>
                    <li @click="backIndex()">上一级</li>
                    <li @click="skipPage(41)">浴室系列</li>
                    <li @click="skipPage(43)">五金系列</li>
                    <li @click="skipPage(44)">定制系列</li>
                    <li @click="skipPage(45)">智能系列</li>
                    <li @click="skipPage(46)">其他</li>
                  </ul>
                </div>
              </li>
              <li  class="m-nav-item-two">
                <div class="m-select-two" v-show="contact">
                  <ul>
                    <li @click="backIndex()">上一级</li>
                    <li @click="skipPage(24)">联系电话</li>
                    <li @click="skipPage(24)">联系地址</li>
                  </ul>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
        name: "heads",
      data(){
        return{
           about:false,
           plan:false,
           product:false,
           software:false,
          contact:false,
          activeClass:'backgroud',
          activeNum:0,
          addBg:'backgroud',
          headerShow:false
        }
      },
      computed:{
          addClassToBg(){
             return this.$store.state.navigation;
          },
        secondNav(){
            return this.$store.state.second_nav;
          },
      },
      watch:{
        secondNav(){
          this.activeNum = this.$store.state.second_nav;
        },
        addClassToBg(){
          if(this.addClassToBg===true){
            this.headerShow=false;
          }
        }
      },
      methods:{
        backIndex(){
          this.headerShow = true;
          this.about = false;
          this.plan = false;
          this.product = false;
          this.contact = false;
        },
        clickMuneShow(){
          this.headerShow = !this.headerShow;
        },
        navShow(val){
            this.headerShow = false;
            if(val===1){
              this.about = true;
            }else if(val===2){
              this.plan = true;
            }else if(val===3){
              this.product = true;
            }else if(val===4){
              this.contact = true;
            }
          },
        navLeave(val){
          if(val==1){
            this.about =false;
          }else if(val==2){
            this.plan = false;
          }else if(val==3){
            this.product = false;
          }
        },
        skipPage(val){
          if(val===1){
            this.$router.push('/');
            this.activeNum = val;
            this.headerShow = false;
          }else if(val===2){
            this.about = false;
            this.$router.push('/about');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','关于我们']);
            this.$store.dispatch('listenToAboutNav',val);
          }else if(val===21){
            this.about = false;
            this.$router.push('/about');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','关于我们','愿景与使命']);
            this.$store.dispatch('listenToAboutNav',val);
          }else if(val===24){
            this.contact = false;
            this.$router.push('/contact');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','联系我们']);
            this.$store.dispatch('listenToAboutNav',val);
          }else if(val===31){
            this.plan = false;
            this.$router.push('/selected');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选龙头']);
            this.$store.dispatch('listenTOsolutionNav',val)
          }else if(val===32){
            this.plan = false;
            this.$router.push('/selected');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选五金']);
            this.$store.dispatch('listenTOsolutionNav',val)
          }else if(val===33){
            this.plan = false;
            this.$router.push('/selected');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选智能']);
            this.$store.dispatch('listenTOsolutionNav',val)
          }else if(val===34){
            this.plan = false;
            this.$router.push('/selected');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选定制']);
            this.$store.dispatch('listenTOsolutionNav',val)
          }else if(val===41){
            this.product = false;
            this.$router.push('/products');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列']);
            this.$store.dispatch('listenTOproductsNav',val);
            this.$store.dispatch('listenTOProType',1);
          }else if(val===43){
            this.product = false;
            this.$router.push('/products');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','产品中心','五金系列']);
            this.$store.dispatch('listenTOproductsNav',val);
            this.$store.dispatch('listenTOProType',9);
          }else if(val===44){
            this.product = false;
            this.$router.push('/products');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','产品中心','定制系列']);
            this.$store.dispatch('listenTOproductsNav',val);
            this.$store.dispatch('listenTOProType',13);
          }else if(val===45){
            this.product = false;
            this.$router.push('/products');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','产品中心','智能系列']);
            this.$store.dispatch('listenTOproductsNav',val);
            this.$store.dispatch('listenTOProType',16);
          }else if(val===46){
            this.product = false;
            this.$router.push('/products');
            this.activeNum = val;
            this.$store.dispatch('listenTOAllTitle',['首页','产品中心','其他']);
            this.$store.dispatch('listenTOproductsNav',val);
            this.$store.dispatch('listenTOProType',19);
          }
          $('html,body').animate({
            scrollTop: 0
          },500)
        }
      }
    }
</script>

<style scoped>
  .headerCenter{
    width: 100%;
    position: relative;
  }
 /*pc端*/
  .backgroud{
    background-color: #363f4d !important;
  }
 .heads{
   display: block;
   position: fixed;
   top: 0;
   width: 100%;
   height: 60px;
   z-index: 100;
 }
  .headContent{
    width: 960px;
    margin: 0 auto;
    display: flex;
  }
  .headLeft{
   flex: 0 0 55%;
     margin-top:5px;
  }
  .headLeft img{
   width: 50px;
  }
  .headRight{
    flex: 0 0 45%;
  }
  .headRight ul{
     width: 100%;
    display:flex;

  }
  .headRight .nav-item{
    flex:1;
    text-align: center;
    color: white;
    line-height: 60px;
    font-size: 14px;
    position: relative;
    cursor:pointer

  }
  .nav-item span{
    display: inline-block;
  }
  .select{
    position: absolute;
    top: 60px;
    width:130px;
    overflow: hidden;
    clear: both;
    height: 270px;
    z-index: 1;
  }
  .select ul{
    width: 100%;
    display: block;
  }
  .select ul li{
    font-size: 14px;
    display: block;
    width: 100%;
    line-height: 45px;
    overflow: hidden;
    background-color: #505e73;
    cursor:pointer;
  }
  .select ul li:hover{
    background-color: #2a333c;
  }
 ul {
   list-style-type: none;
   padding: 0;
 }

  /*移动端*/
  .mobileHeads{
    display: none;
    width: 94%;
    background-color: #363f4d;
    padding: 0 3%;
    height: 50px;
    justify-content: center;
    align-items: center;
  }
  .m-logo{
    margin-top: 5px;
    flex: 0 0 100px;
  }
  .m-logo img{
    width: 40px;
  }
  .m-bottom{
    flex: 1;
    position: relative;
    text-align: right;
    }
  .m-spanBottom{
    display: inline-block;
    text-align: right;
  }
  .m-spanBottom img{
    width: 30px;
    padding-top: 2px;
    height: 30px;
    display: inline-block;
  }
  .m-headRight{
    width: 150px;
    position: absolute;
    top: 42px;
    right: 0;
    z-index: 100;
  }
  .m-headRight .m-nav-item{
    text-align: center;
    width: 100%;
    padding: 10px 0;
    font-size: 15px;
    color: white;
    background-color: #505e73;
    border-bottom: 1px solid #ddd;
  }
  .m-headRight-two{
    width: 150px;
    position: absolute;
    top: 42px;
    right: 0;
    z-index: 100;
  }
  .m-nav-item-two{
    text-align: center;
    width: 100%;
    font-size: 14px;
    color: white;
    background-color: #505e73;
  }
  .m-select-two{
    width: 100%;
    overflow: hidden;
  }
  .m-nav-item-two .m-select-two ul li{
    width: 100%;
    padding: 15px 0;
    border-bottom: 1px solid #cccccc;
  }
  .m-nav-item-two .m-select-two ul li:last-child{
    border-bottom: none;
  }
  @media screen and (max-width: 768px){
    .mobileHeads{
      display: flex;
    }
    .heads{
      display: none;
    }
  }
</style>



