<template>
  <div class="pageFooter">
      <div class="pc-footer">
        <div class="footerTop">
          <div class="footerTopContent">
            <div class="footertext">
              <div class="footertextLeft">
                <div class="Left-item">
                  <h4 @click="skipPage(2)">关于我们</h4>
                  <ul>
                    <li @click="skipPage(2)">公司简介</li>
                    <li @click="skipPage(21)">公司愿景</li>
                  </ul>
                </div>
                <div class="Left-item">
                  <h4 @click="skipPage(31)">精选系列</h4>
                  <ul>
                    <li @click="skipPage(31)">精选龙头</li>
                    <li @click="skipPage(32)">精选五金</li>
                    <li @click="skipPage(33)">精选智能</li>
                    <li @click="skipPage(34)">精选定制</li>
                  </ul>
                </div>
                <div class="Left-item">
                  <h4 @click="skipPage(41)">产品中心</h4>
                  <ul>
                    <li @click="skipPage(41)">浴室系列</li>
                    <li @click="skipPage(43)">五金系列</li>
                    <li @click="skipPage(44)">定制系列</li>
                    <li @click="skipPage(45)">智能系列</li>
                  </ul>
                </div>
                <div class="Left-item">
                  <h4  @click="skipPage(24)">联系我们</h4>
                  <ul>
                    <li @click="skipPage(24)">联系我们</li>
                    <li @click="skipPage(24)">门店地址</li>
                  </ul>
                </div>
              </div>
              <div class="footertextRight">
                <div class="kefu">
                  <h4>客服:</h4>
                  <span>0375-7088900</span>
                </div>
                <div class="kefu">
                  <h4>邮箱:</h4>
                  <span>578736190@qq.com</span>
                </div>
                <div class="kefu">
                  <h4>Q Q:</h4>
                  <span>578736190</span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="footerBottom">
          <div class="footBottomText">
            Copyright 2016-2018 by xxxx有限公司. All Rights Reserved. | 粤ITP备565005453号-8 | 粤网文[2019]0626-1859号
          </div>
        </div>
      </div>
  </div>
</template>

<script>
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  export default {
    data(){
      return {}
    },
    mounted() {

    },
    methods:{
      skipPage(val){
        if(val===2){
          this.$router.push('/about');
          this.$store.dispatch('listenTOsecondNav',2);
          this.$store.dispatch('listenTOAllTitle',['首页','关于我们']);
          this.$store.dispatch('listenToAboutNav',val);
        }else if(val===21){
          this.$router.push('/about');
          this.$store.dispatch('listenTOsecondNav',21);
          this.$store.dispatch('listenTOAllTitle',['首页','关于我们','愿景与使命']);
          this.$store.dispatch('listenToAboutNav',val);
        } else if(val===24){
          this.$router.push('/contact');
          this.$store.dispatch('listenTOsecondNav',24);
          this.$store.dispatch('listenTOAllTitle',['首页','联系我们']);
          this.$store.dispatch('listenToAboutNav',val);
        }else if(val===31){
          this.$router.push('/selected');
          this.$store.dispatch('listenTOsecondNav',31);
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选龙头']);
          this.$store.dispatch('listenTOsolutionNav',val)
        }else if(val===32){
          this.$router.push('/selected');
          this.$store.dispatch('listenTOsecondNav',32);
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选五金']);
          this.$store.dispatch('listenTOsolutionNav',val)
        }else if(val===33){
          this.$router.push('/selected');
          this.$store.dispatch('listenTOsecondNav',33);
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选智能']);
          this.$store.dispatch('listenTOsolutionNav',val)
        }else if(val===34){
          this.$router.push('/selected');
          this.$store.dispatch('listenTOsecondNav',34);
          this.$store.dispatch('listenTOAllTitle',['首页','精选系列','精选定制']);
          this.$store.dispatch('listenTOsolutionNav',val)
        }else if(val===41){
          this.$router.push('/products');
          this.$store.dispatch('listenTOsecondNav',41);
          this.$store.dispatch('listenTOAllTitle',['首页','产品中心','浴室系列']);
          this.$store.dispatch('listenTOproductsNav',val);
          this.$store.dispatch('listenTOProType',1);
        }else if(val===43){
          this.$router.push('/products');
          this.$store.dispatch('listenTOsecondNav',43);
          this.$store.dispatch('listenTOAllTitle',['首页','产品中心','五金系列']);
          this.$store.dispatch('listenTOproductsNav',val);
          this.$store.dispatch('listenTOProType',9);
        }else if(val===44){
          this.$router.push('/products');
          this.$store.dispatch('listenTOsecondNav',44);
          this.$store.dispatch('listenTOAllTitle',['首页','产品中心','定制系列']);
          this.$store.dispatch('listenTOproductsNav',val);
          this.$store.dispatch('listenTOProType',13);
        }else if(val===45){
          this.$router.push('/products');
          this.$store.dispatch('listenTOsecondNav',45);
          this.$store.dispatch('listenTOAllTitle',['首页','产品中心','智能系列']);
          this.$store.dispatch('listenTOproductsNav',val);
          this.$store.dispatch('listenTOProType',16);
        }
        $('html, body').animate({scrollTop: 0}, 1000);
      }
    }
  }
</script>

<style scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.pc-footer{
  width: 100%;
  overflow: hidden;
}
.pageFooter{
  width: 100%;
  overflow: hidden;
}
.footerTop{
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  background-color: #363f4d;
  padding-bottom: 40px;
}
.footerTopContent{
  width: 960px;
  margin:  0 auto;
}
  .footertext{
    margin-top: 25px;
  }
  .footertextLeft{
    width: 60%;
    float: left;
    display: flex;
  }
  .Left-item{
    flex: 1;
    text-align: center;
  }
  .Left-item ul li{
    font-size: 15px;
    color: #6d8299;
    text-align: center;
    margin-top: 10px;
    cursor: pointer;
  }
  .Left-item ul li:hover{
    color: white;
  }
  .Left-item h4{
    cursor: pointer;
    color: white;
  }
  .footertextRight{
    width: 40%;
    float: left;
  }
  .kefu{
    margin-bottom: 8px;
  }
 .kefu h4{
   color: white;
   display: inline-block;
   margin-right: 5px;
 }
  .kefu span{
    font-size: 14px;
    color: #6d8299;
  }
  .footerBottom{
    width: 100%;
    padding: 10px 0;
    background-color: #29313d;
  }
  .footBottomText{
    width: 1300px;
    margin: 0 auto;
    padding: 15px 0;
    color: #6d8299;
    text-align: center;
  }

/*移动端*/
@media screen and (max-width: 768px) {
  .footerTop{
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    background-color: #363f4d;
    padding-bottom: 10px;
  }
  .footerTopContent{
    width: 100%;
    margin:  0 auto;
  }
  .footertext{
    margin-top: 15px;
  }
  .footertextLeft{
    width: 100%;
    float: left;
    display: flex;
  }
  .Left-item{
    flex: 1;
    text-align: center;
  }
  .Left-item ul li{
    color: #6d8299;
    text-align: center;
    margin-top: 5px;
    font-size: 12px;
  }
  .Left-item h4{
    color: white;
    font-size: 14px;
  }
  .footerBottom{
    width: 100%;
    padding: 0px 0;
    background-color: #29313d;
  }
  .footBottomText{
    width: 94%;
    font-size: 12px;
    margin: 0 auto;
    padding: 10px 0;
    color: #6d8299;
    text-align: center;
  }
  .footertextRight{
    display: none;
  }
}
</style>



