import * as type from './mutation-types.js'
export default {
  //需要大写操作，实现提交mutations改变全局
  [type.CHANGE_TABAR](state,value){
    state.navigation = value;
  },
  [type.LISTENIN_ABOUTNAV](state,value){
    state.about_nav = value;
  },
  [type.LISTENIN_SOLUTIONNAV](state,value){
    state.solution_nav = value;
  },
  [type.LISTENIN_PRONAV](state,value){
    state.products_nav = value;
  },
  [type.LISTEIN_SECONDNAV](state,value){
    state.second_nav = value;
  },
  [type.LISTEIN_ALLTITLE](state,value){
    state.indexText = value;
  },
  [type.LISTEIN_PROTYPE](state,value){
    state.products_page = value;
  },

}



