<template>
  <div class="banner" id="banner">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="str in url">
          <img :src="str" alt="">
        </div>
      </div>
      <div class="swiper-pagination swiper-pagination-white"></div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper';
  import 'swiper/dist/css/swiper.min.css';
  import pcBanner1 from "../../../static/newImg/banner1.png";
  import pcBanner2 from "../../../static/newImg/banner2.png";
  import pcBanner3 from "../../../static/newImg/banner3.png";
  import moBanner1 from "../../../static/newImg/m-banner1.png";
  import moBanner2 from "../../../static/newImg/m-banner2.png";
  import moBanner3 from "../../../static/newImg/m-banner3.png";
  export default {
    data(){
      return{
        bannerShow:false,
        width:document.body.clientWidth,
        listImg:[pcBanner1,pcBanner2,pcBanner3],
        mobileListImg:[moBanner1,moBanner2,moBanner3],
        url:[]
      }
    },
    created(){
      this.judgePcOrMobile();
    },
    methods:{
      judgePcOrMobile(){
        if(this.width<768){
          this.url=this.mobileListImg;
        }else {
          this.url=this.listImg;
        }
      }
    },
    mounted() {
      var swiper = new Swiper('.banner .swiper-container', {
        autoplay: true,
        speed: 1500,
        loop: true,
        observer:true,
        observeParents:true,
        autoplayDisableOnInteraction : false,
      });
    }
  }
</script>

<style scoped>
  .swiper-container {
    width: 100%;
    height: 450px;
  }
  .swiper-wrapper {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 100%;
  }
  img {
    width: 100%;
    height: 100%;
    display: block;
  }
  @media screen and (max-width: 768px){
    .swiper-container{
      height: 200px;
    }
  }
</style>



