<template>
  <div id="app">
    <v_head></v_head>
    <v_banner ></v_banner>
    <router-view :proList="proList"></router-view>
    <v_pageFooter></v_pageFooter>
  </div>
</template>

<script>
  import data from './common/util/mock.js'
  import v_head from '@/components/header/header.vue'
  import v_banner from '@/components/banner/banner.vue'
  import v_pageFooter from '@/components/footer/pageFooter.vue'
export default {
  name: 'App',
  components:{
    v_head,
    v_banner,
    v_pageFooter
  },
  data(){
    return{
      proList:{},
    }
  },
  created(){
    this.getProList();
    window.addEventListener('load', this.jumpToIndex());
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  methods:{
    jumpToIndex(){
      $('html,body').animate({
        scrollTop: 0
      },500);
      this.$router.push({
        path: '/index',
        query: {
          id: ''
        }
      });
    },
    handleScroll () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if(scrollTop>0){
         this.$store.dispatch('changeTabar',true)
      }else{
        this.$store.dispatch('changeTabar',false)
      }
    },
    getProList(){
       axios.get('/api/data')
         .then((res)=>{
          this.proList = res.data;
         })
    },
  }
}
</script>

<style>
  *{
    padding:0;
    margin: 0;
  }
  body,html{
    background-color: white;
  }
</style>



