import * as type from './mutation-types.js'
export default {
  changeTabar({commit},value){
      commit(type.CHANGE_TABAR,value);
   },
  listenToAboutNav({commit},value){
      commit(type.LISTENIN_ABOUTNAV,value);
   },
  listenTOsolutionNav({commit},value){
      commit(type.LISTENIN_SOLUTIONNAV,value);
   },
  listenTOproductsNav({commit},value){
      commit(type.LISTENIN_PRONAV,value);
   },
  listenTOsecondNav({commit},value){
      commit(type.LISTEIN_SECONDNAV,value);
   },
  listenTOAllTitle({commit},value){
      commit(type.LISTEIN_ALLTITLE,value);
   },
  listenTOProType({commit},value){
      commit(type.LISTEIN_PROTYPE,value);
   },
}



