import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component:resolve => require(['@/pages/index/index'],resolve)
    },
    {
      path:'/index',
      component:resolve => require(['@/pages/index/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/about',
      component:resolve => require(['@/pages/about/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/selected',
      component:resolve => require(['@/pages/selected/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/products',
      component:resolve => require(['@/pages/products/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/contact',
      name:'contact',
      component:resolve => require(['@/pages/contact/index'], resolve),
    },
    {
      path:'/details',
      component:resolve => require(['@/pages/details/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/afterService',
      component:resolve => require(['@/pages/afterService/index'], resolve),
      meta: {keepAlive: true},
    },
    {
      path:'/problem',
      component:resolve => require(['@/pages/problem/index'], resolve),
      meta: {keepAlive: true},
    },
  ]
})



